import React from 'react';
import DialogTitle from '@material-ui/core/DialogTitle';
import Dialog from '@material-ui/core/Dialog';
import List from '@material-ui/core/List';
import { Map, GroupBy } from 'react-lodash';
import { addLog } from './utils/applogs';
import MenuItem from '@material-ui/core/MenuItem';
import NotificationEventsGroup from './NotificationEventsGroup';

export default function NotificationEventsMenuItem({ onClose }) {

  const [notificationEvents, setNotifictionEvents] = React.useState([]);
  const [openEvents, setOpenEvents] = React.useState(false);

  const handleClickEvents = () => {
    setOpenEvents(false);
    onClose();
  };

  const reloadData = (setNotifictionEvents) => {
    addLog('reloadData');
    fetch(`https://aoq53tdls8.execute-api.us-east-1.amazonaws.com/dev/sensorNotificationEvent`)
      .then(response => response.json())
      .then(json => {
        setNotifictionEvents(json);
      })
      .catch((error) => {
        console.error('ERROR', error);
      });
  }

  const handleOpenEvents = () => {
    setOpenEvents(true);
  };

  React.useEffect(() => {
    reloadData(setNotifictionEvents);
  }, []);

  const snoozeFunction = (sensorId, hours) => {
      addLog('snoozeFunction: ', sensorId);
      fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`,
        {
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          method: 'PATCH',
          body: JSON.stringify({ snoozeUntil: new Date().getTime() + (1000 * hours * 60 * 60) })
        }
      )
        .then(response => response.json())
        .then(json => {
          addLog('Success', json);
        })
        .catch((error) => {
          console.error('ERROR', error);
        });
    };

  return (
    <React.Fragment>
      <MenuItem onClick={handleOpenEvents}>
        Notification Events
      </MenuItem>
      <Dialog onClose={handleClickEvents} aria-labelledby="simple-dialog-title" open={openEvents} fullWidth={true} maxWidth={'xs'}>
        <DialogTitle id="simple-dialog-title">Notification Events</DialogTitle>
        <List>
          <GroupBy collection={notificationEvents} iteratee={(entry) => entry.sensorId}>
            {(value) =>
              <Map collection={Object.keys(value)} iteratee={key =>
                <NotificationEventsGroup
                  eventsList={value[key]}
                  handleClickEvents={handleClickEvents}
                  key={key}
                  snoozeFunction={snoozeFunction}
                />
              } />
            }
          </GroupBy>
        </List>
      </Dialog>
    </React.Fragment>
  )
}