import React, { useState } from 'react';
import SensorItem from './SensorItem'
import { addLog } from './utils/applogs';
import { makeStyles } from '@material-ui/core/styles';
import {
  SwipeableList,
  SwipeableListItem
} from '@sandstreamdev/react-swipeable-list';
import '@sandstreamdev/react-swipeable-list/dist/styles.css';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    maxWidth: 440,
    backgroundColor: theme.palette.background.paper,
  },
}));

const markSensorAsFavourite = (sensorId) => {
  addLog('markSensorAsFavourite: ', sensorId);
  fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`,
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: 'PATCH',
      body: JSON.stringify({ isFavourite: true })
    }
  )
    .then(response => response.json())
    .then(json => {
      addLog('Success', json);
    })
    .catch((error) => {
      console.error('ERROR', error);
    });

};

export default function SensorList({ sensorData }) {
    const classes = useStyles();
    const items = Object.keys(sensorData.items)
      .sort((a, b) => sensorData.items[a][0].name.localeCompare(sensorData.items[b][0].name));

    const swipeRightOptions = sensorId => ({
      content: <div>Favourite!</div>,
      action: () => markSensorAsFavourite(sensorId)
    });

    return (
      <>
        <SwipeableList className={classes.root}>
          {
            items.map((item) =>
              <SwipeableListItem key={item}
                swipeRight={swipeRightOptions(item)}
              >
                <SensorItem 
                  sensors={sensorData.items[item]}
                />
              </SwipeableListItem>
            )
          }
        </SwipeableList>
      </>
   )
}