import React from 'react';
import { addLog } from './utils/applogs';
import Grid from '@material-ui/core/Grid';
import ValueButton from './ValueButton';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/core/styles';
import { Map } from 'react-lodash';
import FavoriteIcon from '@material-ui/icons/Favorite';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    maxWidth: 440,
    marginTop: 10,
    marginBottom: 10
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    fontSize: 16,
    fontWeight: 700
  },
  fav: {
    color: '#FFFFFF',
    position: 'absolute',
    '&:hover': {
      color: '#444444',
    },
  }
}));

const loadSensors = (setSensors) => {
  addLog('loadSensors');
  fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor`)
    .then(response => response.json())
    .then(json => {
      console.log('loadSensors', json);
      setSensors(json.sensors);
    })
    .catch((error) => {
      console.error('ERROR', error);
    });
}

const unmarkSensorAsFavourite = (sensorId) => {
  addLog('unmarkSensorAsFavourite: ', sensorId);
  fetch(`https://3gzxlzr6d4.execute-api.us-east-1.amazonaws.com/dev/sensor/${sensorId}`,
    {
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
      },
      method: 'PATCH',
      body: JSON.stringify({ isFavourite: false })
    }
  )
    .then(response => response.json())
    .then(json => {
      addLog('Success', json);
    })
    .catch((error) => {
      console.error('ERROR', error);
    });

};

export default function SensorsFavourites({ sensorData }) {
  const [sensors, setSensors] = React.useState([]);

  React.useEffect(() => {
    loadSensors(setSensors)
  }, []);

  const [favouriteSensors, setFavouriteSensors] = React.useState([]);
  React.useEffect(() => {
    setFavouriteSensors(
      sensors.filter(sensor => sensor.isFavourite)
      .map(sensor => sensor.sensorId)
    );
  }, [sensors]);

  const classes = useStyles();

  return (
    <Grid container spacing={3} className={classes.root}>
      <Grid item xs={12}>
        <Grid container spacing={1}>
          <Map collection={favouriteSensors} iteratee={sensorId =>
            <Grid item xs={6} key={sensorId}>
              <Paper className={classes.paper}>
                <FavoriteIcon className={classes.fav} onClick={() => unmarkSensorAsFavourite(sensorId)} />
                <p>
                  {sensorData.items[sensorId] && sensorData.items[sensorId][0].name}
                </p>
                <div>
                  <Map collection={sensorData.items[sensorId]} iteratee={sensor =>
                    ['temp', 'humidity'].includes(sensor.type) ? <ValueButton sensor={sensor} key={sensor.type} /> : ''
                  } />
                </div>
              </Paper>
            </Grid>
          } />
        </Grid>
      </Grid>
    </Grid>
  );
}
